a {
  text-decoration: none;
  color: #f1faee;
}

.contact {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-left: 1rem;
  margin-right: 1rem;
}

.contact > p {
  width: 100%;
}

.contact .item {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 3rem;
  font-family: "Nunito";
}

.contact .item > a {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: -1.7rem;
  margin-top: -22px;
}

.contact .item > p {
  margin-top: 0.5rem;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.experience {
  max-width: 90%;
}

.introduction {
  display: flex;
  align-items: center;
  width: 85%;
  margin-top: 40px;
}

.introduction > img {
  max-width: 100%;
}

.introduction .text {
  margin-left: 3.5rem;
}

.introduction .title {
  margin-left: 2.5rem;
}

.knowledge {
  max-width: 90%;
}

.knowledge > p {
  font-family: "Nunito";
  font-weight: bold;
  font-size: 1.8rem;
  text-align: center;
  margin-top: 2rem;
}

.logo {
  height: 5rem;
  width: 5rem;
  object-fit: contain;
}

.logo-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  width: 7.5rem;
  height: 10.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.logo-container > p {
  visibility: none;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s;
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Nunito";
  font-weight: 300;
  font-size: 0.9rem;
  text-align: center;
}

.logo-container:hover > p {
  visibility: visible;
  opacity: 1;
}

.logo-large {
  max-width: 5rem;
}

.logo-group {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.logo-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background-color: #333;
  height: 7.5rem;
  width: 7.5rem;
  display: flex;
  border-radius: 1rem;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
}

.logo-wrapper:hover {
  opacity: 0.7;
  height: 8rem;
  width: 8rem;
}

.main {
  background-color: #777;
  color: #f1faee;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navbar {
  width: 100%;
  height: 4rem;
  background-color: #7531c1;
  display: flex;
  align-items: center;
}

.navbar > h2 {
  margin-left: 2rem;
  margin-right: 8rem;
}

.navbar .options {
  width: 70%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbar .options > div {
  width: 100%;
  height: 1.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid pink;
}

.navbar .options > div:last-of-type {
  border-right: none;
}

.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90%;
}

.text {
  font-size: 1.5rem;
  font-family: "Nunito";
}

.title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
}

@media (max-width: 800px) {
  .introduction {
    flex-wrap: wrap;
  }
  .introduction .text {
    margin-left: 0;
  }

  .contact {
    flex-direction: column;
  }
}
